@import "../../styles/variables";

.last-search {
    &__title {
        font-size: $font-size-h2;
    }

    &__list {
        margin: 0;
        padding: 0;
        list-style-type: none;
    }

    &__list-item {
        cursor: pointer;
        user-select: none;

        &--active {
            text-decoration: underline;
        }
    }
}
